let cardsNumber=0;

function changeV(elem){

    if(elem.innerHTML == 'VISUAL<h2>X</h2>'){
        if(visualTag == 0) { visualTag=1 } else { visualTag=0 }
    }
    if(elem.innerHTML == 'VR/AR<h2>X</h2>'){
        if(vrArTag == 0) { vrArTag=1 } else { vrArTag=0 }
    }
    if(elem.innerHTML == 'PRODUCT<h2>X</h2>'){
        if(productTag == 0) { productTag=1 } else { productTag=0 }
    }
    if(elem.innerHTML == 'UX/UI<h2>X</h2>'){
        if(uxUiTag == 0) { uxUiTag=1 } else { uxUiTag=0 }
    }

    
    removeCards()
    createCards()
    
}


let vrArTag = 1
let vrAr = (i,j) => projects[i].tags[j]=='VR/AR' && vrArTag==1 || (vrArTag==0 && uxUiTag==0 && productTag==0 && visualTag==0);

let uxUiTag = 1
let uxUi = (i,j) => projects[i].tags[j]=='UX/UI' && uxUiTag==1 || (vrArTag==0 && uxUiTag==0 && productTag==0 && visualTag==0);

let productTag = 1
let product = (i,j) => projects[i].tags[j]=='PRODUCT' && productTag==1 || (vrArTag==0 && uxUiTag==0 && productTag==0 && visualTag==0);

let visualTag = 1
let visual = (i,j) => projects[i].tags[j]=='VISUAL' && visualTag==1 || (vrArTag==0 && uxUiTag==0 && productTag==0 && visualTag==0);





function createCards(){
    let flag=0;
    cardsNumber=0;

    for(let i=0; i<projects.length; i++){
        flag=0;
        for(let j=0; j<projects[i].tags.length; j++){
            if(vrAr(i,j) || uxUi(i,j) || product(i,j) || visual(i,j)){
                flag=1;
            }
        }
        if(flag==1){createCard(projects[i].name, projects[i].tags, projects[i].img, cardsNumber); cardsNumber++; }
        
    }
    document.getElementById('projectsArchiveTitle').innerHTML = 'Projects Archive' + ' (' + cardsNumber + ')'
    
}



function removeCards(){
    let paras = document.getElementsByClassName('card');

    while(paras[0]) {
        paras[0].parentNode.removeChild(paras[0])
    }
}
  
createCards()


